.functioning_main {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: auto;
    margin-bottom: 20vh;
    flex-direction: column;
}

.functioning_content {
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 85%;
    min-height: 55vh;
}

.functioning_content_box {
    display: flex;
    margin: auto;
    flex-direction: column;
    background-color: black;
    height: 100%;
    width: 65%;
    color: white;
    padding: 4%;
    border-radius: 50px;
}

.functioning_content_title {
    display: flex;
    font-weight: bolder;
    font-size: 35px;
    color: white;
    line-height: 4vw;
    width: 60%;
}

.functioning_content_text {
    display: flex;
    color: white;
    font-size: 1.1rem;
    flex-direction: column;
}

.functioning_content_button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 23%;
    height: 6vh;
    font-weight: bold;
    background-color: #0096D1;
    transition: background-color 0.3s ease, color 0.3s ease, transform 0.3s ease;
    margin-top: 3vh;
    border-radius: 20px;
    font-size: 15px;
}

.functioning_content_button:hover {
    background-color: #00adf2;
    color: white;
    transform: scale(1.0);
}

.functioning_apresention_main {
    display: flex;
    align-items: center;
    width: 100%;
    height: auto;
    flex-direction: column;
    margin-top: 5vh;
}

.functioning_apresention_content {
    display: flex;
    flex-direction: column;
    width: 70%;
    align-items: center;
}

.functioning_apresention_texts {
    font-weight: bolder;
    font-size: 37px;
    margin: 0;
    padding: 0px 46% 30px 140px;
}

.functiong_service_columns_container {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 0 10%;
    gap: 40px;
}

.functiong_service_vertical {
    display: flex;
    flex-direction: column;
    width: 48%;
    gap: 20px;
}

.functiong_service_colune1,
.functiong_service_colune2,
.functiong_service_colune3,
.functiong_service_colune4,
.functiong_service_colune5 {
    color: rgb(0, 0, 0);
    padding: 30px;
    border-radius: 20px;
}

.functiong_service_title {
    padding: 40px 0px 6px 0px;
    font-weight: bold;
    font-size: 20px;
}

.functiong_service_text {
    font-size: 1rem;
    color: rgb(0, 0, 0);
}

/* RESPONSIVIDADE */
@media (max-width: 768px) {
    .functiong_service_columns_container {
        flex-direction: column;
    }

    .functiong_service_vertical {
        width: 100%;
    }
}